html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ul {
  list-style: none;
  padding: 0;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ul {
  list-style: none;
  padding: 0;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.center-content {
  max-width: 800px;
}

.wrap-header {
  padding: 2rem;
  min-height: fit-content;
  background: #333333;
}
.wrap-header .wrapper-header {
  height: auto;
}
.wrap-header.home {
  background: url("../img/header.jpg") no-repeat center center/cover;
  background-attachment: fixed;
}
@media (min-width: 768px) {
  .wrap-header.home {
    min-height: 100vh;
  }
}
.wrap-header.home .wrapper-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}
.wrap-header.home .wrapper-header h2 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  margin-bottom: 5rem;
}

.wrap-navbar {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .wrap-navbar {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .wrap-navbar--navigation {
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    gap: 2rem;
  }
}
.wrap-navbar--navigation .link {
  color: #FFFFFF;
  text-decoration: none;
  display: block;
  text-align: center;
}

.wrap-footer {
  background: #333333;
  padding: 2rem 0;
}
.wrap-footer .wrapper-footer--copy {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .wrap-blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.wrap-blog .blog .blog-entry {
  margin-bottom: 2rem;
}
.wrap-blog .blog .blog-entry:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .wrap-blog .blog .blog-entry {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
  }
}
.wrap-blog .blog .blog-entry img {
  border-radius: 1.5rem;
}
.wrap-blog .blog .blog-entry--text a {
  color: #000000;
  display: block;
  margin-top: 1.5rem;
}
@media (min-width: 768px) {
  .wrap-blog .blog .blog-entry--text a {
    margin-top: 0;
  }
}
.wrap-blog .blog .blog-entry--text h4 {
  margin: 0;
}
.wrap-blog .blog .blog-entry--text h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background: #71B100;
  margin-top: 0.5rem;
}
.wrap-blog .blog .blog-entry--text span {
  color: #E08709;
}

.wrap-contact {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  background: url("../img/encuentra.jpg") no-repeat center center/cover;
  background-attachment: fixed;
  padding: 5rem 0;
}
.wrap-contact h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.wrap-contact p {
  font-size: 1.8rem;
  text-align: center;
  color: #FFFFFF;
}
.wrap-contact .wrap-contact-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}
.wrap-contact .wrap-contact-btn .btn.btn-warning {
  background: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-flex;
  border-radius: 1rem;
  transition: background 0.2s ease-in-out;
}
.wrap-contact .wrap-contact-btn .btn.btn-warning:hover {
  background: #af6907;
}

@media (min-width: 768px) {
  .wrap-main--icons {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}
.wrap-main--icons .icon {
  text-align: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .wrap-main--icons .icon {
    margin-top: 0;
  }
}
.wrap-main--icons .icon img {
  height: 10rem;
}
.wrap-main--icons .icon h3, .wrap-main--icons .icon p {
  margin: 0;
}

@media (min-width: 768px) {
  .wrap-publish {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.wrap-publish .wrapper-publish {
  background: #e4e4e4;
  border: 1px solid #bbbbbb;
  border-radius: 1.5rem;
  overflow: hidden;
  margin-top: 3rem;
}
.wrap-publish .wrapper-publish:first-of-type {
  margin-top: 0;
}
@media (min-width: 768px) {
  .wrap-publish .wrapper-publish {
    margin-top: 0;
  }
}
.wrap-publish .wrapper-publish .description-publish {
  padding: 2rem;
}
.wrap-publish .wrapper-publish .description-publish h3, .wrap-publish .wrapper-publish .description-publish p {
  margin: 0;
}
.wrap-publish .wrapper-publish .description-publish .price-publish {
  color: #71B100;
  font-size: 3rem;
  font-weight: 700;
}
.wrap-publish .wrapper-publish .description-publish .properties-publish {
  display: flex;
  box-sizing: border-box;
  justify-content: space-around;
  align-items: center;
  margin-top: 2rem;
}
.wrap-publish .wrapper-publish .description-publish .properties-publish li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 2rem;
}
.wrap-publish .wrapper-publish .description-publish .properties-publish li:last-of-type {
  margin-right: 0;
}
.wrap-publish .wrapper-publish .description-publish .properties-publish li img {
  margin-right: 1rem;
}
.wrap-publish .wrapper-publish .description-publish .btn.btn-warning {
  background: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border-radius: 1rem;
  transition: background 0.2s ease-in-out;
}
.wrap-publish .wrapper-publish .description-publish .btn.btn-warning:hover {
  background: #af6907;
}

.wrap-btn-all {
  display: flex;
  box-sizing: border-box;
  justify-content: flex-end;
  align-items: center;
  margin-top: 3rem;
}
.wrap-btn-all .btn.btn-success {
  background: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border-radius: 1rem;
  transition: background 0.2s ease-in-out;
}
.wrap-btn-all .btn.btn-success:hover {
  background: #507e00;
}

.wrap-testimonies .testimony {
  background: #71B100;
  border-radius: 1.5rem;
  font-size: 2.4rem;
  color: #FFFFFF;
  height: fit-content;
  padding: 2rem;
}
.wrap-testimonies .testimony blockquote {
  padding-left: 5rem;
  position: relative;
}
.wrap-testimonies .testimony blockquote::before {
  content: "";
  background: url("./../img/comilla.svg") no-repeat;
  position: absolute;
  top: 0;
  left: -2.5rem;
  display: block;
  width: 6rem;
  height: 8rem;
}
.wrap-testimonies .testimony p {
  text-align: right;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .wrap-about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.wrap-about--description blockquote {
  font-size: 2.2rem;
  font-weight: 900;
  padding: 1rem 0 3rem 0;
  margin: 0;
}